import React, { useLayoutEffect } from 'react';
import './styles/googleSignIn.css';
import GoogleButton from 'react-google-button'
import { useLocation, useNavigate } from 'react-router-dom';

const GoogleSignIn = () => { 
    const location = useLocation();
    const navigate = useNavigate();
    useLayoutEffect(() => {
        const queryParams = new URLSearchParams(location.search);
        const isSuccess = queryParams.get("success")
        if(isSuccess === 'success'){
            localStorage.setItem('isGoogleConnected', true);
            navigate('/app')
        }
        if(localStorage.getItem('isGoogleConnected')) {
            navigate('/app')
        }
    }, [])
    return (
        <div className='signInGoogleDiv'>
            <p className='signInGoogleText'>Sign into your Google Calendar to access Meetzflow's awesome features</p>
            <GoogleButton
                onClick={() => {window.open('https://meetzflow.com/auth/google')}}
                style={{margin: 'auto'}}
            />
        </div>
    )
}

export default GoogleSignIn;